export enum MediaSeason {
    WINTER = "WINTER",
    SPRING = "SPRING",
    SUMMER = "SUMMER",
    FALL = "FALL",
}

// Query.Media(season: MediaSeason, seasonYear: Int)
export const querySeason = `
query ($season: MediaSeason, $seasonYear: Int) {
    Media (season: $season, seasonYear: $seasonYear) {
        id
        title {
            romaji
            english
            native
        }
        episodes
        chapters
        volumes
        isAdult
        genres
        source
    }
}
`;
